import React, { useEffect, useState } from 'react'
import { Link, useHistory, useParams } from 'react-router-dom';
import axios from '../api'
import { Header } from "../components/Header";
import dp from "../assets/dp.svg"
import './UserPage.css'
import FeatherIcon from "feather-icons-react";
import { WhatsAppBtn } from "../components/WhatsAppBtn";
import { Loading } from "../components/Loading";
import walogo from "../assets/whatsapp.svg"
import { useInView,InView } from 'react-intersection-observer';
import { GridBox } from '../components/GridBox';
import StackGrid from "react-stack-grid";
import Masonry, {ResponsiveMasonry} from "react-responsive-masonry"
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const copy = require('clipboard-copy')
const ls = require("local-storage")
export const UserPage = ({ match }) => {
  var [user, setuser] = useState([])
  var [loading, setloading] = useState(true);
  var [err, seterr] = useState(false);
  var [tab1post,settab1post]=useState([])
  var [skip1,setskip1]=useState(0)
  var [stopfetch1,setstopfetch1]=useState(false)
  var [copied, setcopied]=useState("")
  var { id } = match.params
  async function Getposts() {
    //setloading(true)
    seterr(false)
    try {      
      var queryparams=""
      var url= "/posts?"
      var x=skip1
      queryparams+="skip="+ x +"&category="+ user._id
     
      const response = await axios.get(url+queryparams);
      console.log(response.data)
    
      settab1post( tab1post.concat(response.data))
      setskip1(skip1+10)       
      if(!response.data[0] || response.data.length <10){setstopfetch1(true);
        console.log("stoped");}
     
      //setloading(false)
    } catch (error) {
      seterr(error);
      //setloading(false)
    }
  }
  async function Getuser() {
    setloading(true)
    seterr(false)
    try {
      let { id } = match.params
      var url = id == 'me' ? '/users' : '/users/' + id
      const response = await axios.get(url);
      console.log(response.data);
      setuser(response.data)
      setloading(false)
    } catch (error) {
      seterr(error);
      setloading(false)
    }
  }
  useEffect(() => {
    console.log("effect")
    Getuser()
  }, [])
  function notify() {  toast.dark('Link copied !!!', {
    position: "bottom-center",
    autoClose: 2000,
    hideProgressBar: true,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    });
  }
  
  return (
    <div className="mb-5" >
      <Header login={false} ></Header>
      <ToastContainer />
      {err  &&  <h1 className="mt70" >User not Found.</h1>  }
      {!loading&& !err && <> <div className="mt70" >
      
        <div className="mt-3" >
          <div className="row" >
            <div className="col-5 pr-0" >
            <img className="shadow dp2 mt-1" src={user.profilepic ? user.profilepic : dp} />
           
            </div>
            <div className="col-7" > 
             <h3 className="mb-0" >{user.name} </h3>
             <div className="mt-1 text14 font-weight-bold " > {user.description}</div>
        
        <div className=" mt-1 text-small font-weight-bold text-muted" ><FeatherIcon size={12} icon="map-pin" ></FeatherIcon> {user.address + ", " + user.state + ", " + user.country} </div>

      
            {user._id == ls("user_id") ?
            <Link to={"/editprofile"} > <button className="btn btn-light mt-2  btn-sm " > <FeatherIcon icon="edit" size={18} ></FeatherIcon> Edit profile</button></Link>:<></>}
             </div>
        
          </div>
         
          <button className="btn  btn-primary btn-block mt-3" onClick={()=>{copy(window.location.host+window.location.pathname+"/#/user/"+user._id);notify()}} >
            <FeatherIcon icon="share-2" size={16} ></FeatherIcon> Share Shop
            </button>

          {user._id == ls("user_id") ? <div className=" p-2 " >
            <div className="mt-2 " >
              <Link to={"/addpost?type=category&category="+user._id} >
              <button className="btn btn-dark btn-sm" > <FeatherIcon icon="plus" ></FeatherIcon> Add category </button>
              </Link>
              <Link to={"/addpost?type=product&category="+user._id} >
              <button className="btn btn-dark btn-sm ml-2 " > <FeatherIcon icon="plus" ></FeatherIcon> Add product </button>
              </Link>
            </div>
            
          </div> : <>
            </>}

        </div>
        <div>
        </div>

        <div className=" mt-3 mb-5">      
          <div class="">
          <ResponsiveMasonry
                columnsCountBreakPoints={{350: 2, 750: 3, 900: 3}}
            >
      <Masonry gutter="5px">
          { tab1post.map((post)=> <GridBox key={post._id} data={post} setcopied={setcopied} ></GridBox> )  }
          </Masonry>
          </ResponsiveMasonry>
          </div>
          {!stopfetch1 &&
            <InView as="div" onChange={(inView, entry) =>{if(inView){Getposts()}}}>
             <div className="container mb-4 text-center"  ><div className="spinner-border text-primary" role="status"  ></div>
              </div>
              </InView>}
              
        </div>
      </div>
      <WhatsAppBtn num={user.whatsapp} ></WhatsAppBtn>
      </>
    }
      {loading&& <Loading></Loading> }
    </div>
  )
}
